import 'whatwg-fetch';
import axios from 'axios';
import { 
  GET_COMMENTS_BY_POST,
  POST_COMMENT,
  DELETE_COMMENT,
  UPDATE_COMMENT } from './constants';
import { status, json } from '../../utils/fetch';

export const getCommentsByPost = id =>
  dispatch =>
    axios.get(`https://jsonplaceholder.typicode.com/posts/${id}/comments`)
      // .then(status)
      // .then(json)
      .then(response => dispatch({ type: GET_COMMENTS_BY_POST, comments: response.data }))
      .catch(err => console.log(err));

export const postComment = comment =>
  dispatch =>
    axios.post(`https://jsonplaceholder.typicode.com/comments`, {
      name: comment.name,
      email: comment.email,
      body: comment.body
    })
      // .then(json)
      .then(response => dispatch({ type: POST_COMMENT, comment: response.data }))
      .catch(err => console.log(err));

export const deleteComment = id =>
  dispatch =>
    axios.delete(`https://jsonplaceholder.typicode.com/comments/${id}`)
      // .the(json)
      .then(() => dispatch({ type: DELETE_COMMENT, id }))
      .catch(err => console.log(err));
 
export const updateComment = (id, updatedComment) =>
  dispatch =>
    axios.put(`https://jsonplaceholder.typicode.com/comments/${id}`, updatedComment)
      // .then(json)
      .then(response => dispatch({ type: UPDATE_COMMENT, id, comment: response.data }))
      .catch(err => console.log(err));



